import React,{useEffect} from 'react';
import {getProduct} from '../actions/products';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";

const ProductPage = (props) => {
  
    useEffect(()=>{
      console.log('id is');
console.log(props.match.params.id);
let id=props.match.params.id;
    props.getProduct(id)
    },[])

  return (
    <>
        
        <div className="col-12">
            <h4 className="text-center mt-3">Product Page</h4>
            
            <div className="row">
              <div className="col-4">
              <Link to={`/`}><h3>Back</h3></Link>
                <h3></h3>
                <div>
                    <img src={props.productItem.image} alt='...'/>
                </div>
                
              </div>
              <div className="col-4">
                <div className="mt-4">
                    <h5>{props.productItem.title}</h5>
                    <p>{props.productItem.description}</p>
                    <p>{Math.round(props.productItem.price)}</p>
                    <p>{props.productItem.category}</p>
               </div>
              </div>
              <div className="col-4"></div>
            </div>
            
        </div>
    </>
  );
};

const mapStateToProps = state => {
  console.log('store');
  console.log(state.productReducer.item);
    return {
      
      productItem:state.productReducer.productItem,
      
    }
  }
  
  const mapDispatchToProps = dispatch => {
    return {
        getProduct: (id) => dispatch(getProduct(id))
    }
  }

export default connect(mapStateToProps,mapDispatchToProps)(ProductPage);

